import React ,{ useState ,useContext} from "react";
import Context from "./context";
import { useSelector } from "react-redux";

const CreateAndLogin = (props) => {
  let authtoken=useSelector((state)=>{return state.lelo.auth});
  // console.log(authtoken)
    async function CreateAccount( data,url = 'https://amazon-clone-backend-q0iu.onrender.com/Account/') {
        const response = await fetch(url, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
        body: JSON.stringify(data)  
        });
        const json =await response.json()
        console.log("NewAccountcreated",json)
        return json.sucess
      }
    async function LoginAccount( data,url = 'https://amazon-clone-backend-q0iu.onrender.com/Account/login') {
        const response = await fetch(url, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
        body: JSON.stringify(data)  
        });
        const json =await response.json()
        console.log("Account logged in",json.sucess)
        return [json.sucess,json.result]
      }
      async function additems( data,url = 'https://amazon-clone-backend-q0iu.onrender.com/Cart/additem') {
        const response = await fetch(url, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            "auth":authtoken
          },
        body: JSON.stringify({"Product":JSON.stringify(data)}) 
        });
        console.log("item added successfully",response)
      }
      async function getitems( data,url = 'https://amazon-clone-backend-q0iu.onrender.com/Cart/getitem') {
        const response = await fetch(url, {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
            "auth":data
          },
        
        })
        // .then((res)=>{console.log(res.json())})
        const arr=await response.json();
        console.log("item get successfully",arr)
        return arr
      }
      async function deleteitems( auth,data,url = 'https://amazon-clone-backend-q0iu.onrender.com/Cart/deletenote') {
        const response = await fetch(url, {
          method: 'DELETE',
          headers: {
            'Content-Type': 'application/json',
            "auth":auth
          },
          body: (JSON.stringify(data) )
          // console.log(stringify({Product:data}))
        
        })
        // .then((res)=>{console.log(res.json())})
        const arr=await response.json();
        console.log("item deleted successfully",arr)
        return arr
      }
  return (
    <Context.Provider value={{ CreateAccount,LoginAccount,additems,getitems,deleteitems}}>
            {props.children}
        </Context.Provider>
  )
}

export default CreateAndLogin
