import React from 'react'
import './CSS/Alert.css'
import { useState } from 'react'

const Alert = (props) => {
    const hide=()=>{
        const cls=document.querySelector('.alert');
        cls.style.display='none';
    }
    
  return (
    <div>
      <div className="alert" style={{"display":props.showalert ? "block" : "none","backgroundColor":(props.ok)?"green":"red"}}>
  <span className="closebtn" onClick={()=>{hide()}} >&times;</span>
  {props.message}
</div>
    </div>
  )
}

export default Alert
